.userProfile {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    height: 40px;
    max-height: 10%;
    margin: 70px auto 0 auto;
    /* padding: 20px; */
    font-size: 10px;
    color: white;
}

.userDetails {
    display: flex;
    align-items: center;
}

.userWallet {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: auto;
    border-radius: 10px;
    text-align: center;
}

.userDetails,
.userWallet {
    min-height: 40px;

    background-color: #DF8806;
    /* border-radius: 50px; */
    border-top-left-radius: 40px;
    border-top-right-radius: 40px;
    border-bottom-left-radius: 40px;
    border-bottom-right-radius: 40px;
    width: 45%;
}

.userImage {
    min-width: 40px;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    overflow: hidden;
}

.userImage img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}


.Details {
    margin-left: 10px;
    border: none;
    overflow: wrap;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: auto;
}

.Details h4,
.userWallet h4 {
    margin: 2px;
}