.userSideBar {
    position: fixed;
    top: 0;
    left: 0;
    width: 250px;
    max-width: 20%;
    height: 100%;
    background-color: #EDCC9B;
    display: flex;
    flex-direction: column;
    padding: 20px;
}

.userSideBar button {
    font-size: 22px;
}


@media screen and (min-width: 768px) {
    .userSideBar {
        /* margin-top: 60px; */
        padding-top: 90px;
    }
}

/* Hide icons on larger screens */
@media (min-width: 768px) {
    .nav-btn i {
        display: none;
    }
}


/* Mobile version */
.mobileUserSideBar{
    width: 100%;
    max-width: none;
    height: 50px;
    max-height: 10%;
    position: fixed;
    /* z-index: 5; */
    bottom: 0;
    left: 0;
    right: 0;
    background-color: #EDCC9B;
    /* #EDEA9B; */
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    padding: 5px 0;
}

.nav-btn {
    background-color: transparent;
    margin: 0;
    border: none;
    color: white;
    cursor: pointer;
    padding: 5px;
}

.mobileUserSideBar button {
    font-size: 12px;
    font-weight: 500;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.icon {
    /* display: block; */
    width: 25px;
    height: 25px;
    background-size: contain; /* Adjust the background size as needed */
    margin-bottom: 5px;
}

.menu{
    background-image: url('../../../../public/images/menu_icon.png');
}

.shopping-cart{
    background-image: url('../../../../public/images/shopping_cart_icon.png');
}

.feedback{
    background-image: url('../../../../public/images/feedback_icon.png');
}

.settings{
    background-image: url('../../../../public/images/settings_icon.png');
}
