
.userMenuContainer h2{
    font-size: 20px;
    font-weight: 600;
    color: #DF8806;
    margin: 20px 5px 5px 10px;
}


.userMenuInnerContainer {
    display: flex;
    flex-direction: column;
    margin-bottom: 70px ;
}

.menuItem {
    height: 120px;
    max-height: 140px;
    width: 90%;
    background-color: #EDEA9B;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 10px;
    margin: 3px auto 3px auto;
    border-radius: 10px;
}

.menuImage {
    width: 50%;
    height: 90%;
    margin-right: 10px;
}

.menuImage img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 10px;
}

.item {
    width: 50%;
    height: 90%;
    display: flex;
    flex-direction: column;
    justify-content: space-between ;
}

.itemDetails {
    height: 70%;
    margin: 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    color: #DF8806;
    overflow: auto;
    font-size: 18px;
}

.itemDetails h3 {
    margin-top: 2px;
}

.quantity {
    background-color: white;
    color: #DF8806;
    height: 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 10px;
}

.quantity h3{
    margin: 0;
}

.quantity button {
    background-color: transparent;
    border: none;
    padding: 5px 20px;
    margin: auto 0;
    color: #DF8806;
    font-size: 18px;
    font-weight: 700;
}

