.bar{
    height : 30px;
    margin: 20px;

}

.cart{
    position: fixed;
    margin: 0 auto 0 auto;
    height :45px;
    width: 90%;
    bottom: 70px;
    background-color: #DF8806;
    color: white;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    border-radius: 10px;
}

.cart h3{
    margin: 0;
}
