.home-page {
    display: flex;
    flex-direction: column;
    align-items: center;
    /* Center items horizontally */
    justify-content: center;
    /* Center items vertically */
    gap: 20px;
    /* Add a gap between the divs */
    height: 100vh;
    /* Ensure the container takes up the full viewport height */
}

.InnerBox {
    width: 90%;
}

.heading-subtext h1 {
    text-align: center;
    /* Center align the h1 text */
    margin-bottom: 0px;
    margin: 0;
    font-size: 35px;
    font-family: "Monoton", sans-serif;
    font-weight: 400;
    font-style: normal;
    color: #C2255E;
}

.heading-subtext p {
    margin-top: 0;
    /* Remove the space above the p */
    margin-bottom: 40px;
    text-align: center;
    color: #C2255E;
}

.Profiles {
    display: flex;
    flex-direction: column;
    /* Default layout for smaller screens */
    align-items: center;
    /* Center items horizontally */
}

.profile-box {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    width: 90%;
    height: 80px;
    padding: 0 10px;
    border: 2px solid #C2255E;
    ;
    /* Existing border */
    border-radius: 20px;
    /* Add rounded corners */
    background-color: #E3EEA3;
    /* Existing background color */
    margin: 0px auto 40px auto;
    /* Add space around each box */
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.1),
        /* Right and bottom shadow */
        0px 5px 10px rgba(0, 0, 0, 0.1);
    /* Bottom shadow */
}

.profile-box p {
    margin: 0;
}

.HomeIcon {
    font-weight: 700;
    width: 70px;
    height: 70px;
    background-size: contain;
    border-radius: 50%;
}

.HomeUserIcon {
    background-image: url('../../../public/images/HomeUserIcon.png');
}

.HomeReceptionIcon {
    background-image: url('../../../public/images/HomeReceptionIcon.webp');
}

.HomeAdminIcon {
    background-image: url('../../../public/images/HomeAdminIcon.jpg');
}


