/* Media query for screens larger than 768px (typical breakpoint for tablets and desktops) */
@media (min-width: 768px) {
    .Profiles {
        flex-direction: row;
        /* Arranges items horizontally on larger screens */
    }

    .profile-box {
        width: 300px;
    }
}