/* LoginPage.css */
.login-page {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-color: #f0f0f0;
}


.login-box {
    width: 300px;
    background-color: #ffffff;
    padding: 20px;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.login-text {
    text-align: center;
    color: green;
    font-size: 24px;
    margin-bottom: 20px;
}

.input-fields {
    display: flex;
    flex-direction: column;
}

.input-field {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 10px;
}

.input-field input {
    flex-grow: 1;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
}

.input-field img {
    width: 20px;
    height: 20px;
}

.login-button {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 20px;
}

.text-container {
    display: flex;
    flex-direction: column;
}

.button-container button {
    background-color: green;
    color: #ffffff;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
}

.green-bar {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 5rem;
    /* max-height: 8%; */

    /* Adjust the height as needed */
    background-color: #A0B426;
    /* Center the text vertically */
}

.userIcon {
    font-weight: 700;
    width: 25px;
    height: 25px;
    background-size: contain;
    background-image: url('../../../public/images/icon_user.svg');
}

.passwordIcon {
    font-weight: 700;
    width:25px;
    height:25px;
    background-size: contain;
    background-image: url('../../../public/images/icon_password.svg');
}