.reception-orders-container {
    padding: 40px;
    margin-top: 20px;
}

.reception-order {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: space-between;
}

.order-card {
    background-color: #C6D3E7;
    border: 1px solid #dee2e6;
    border-radius: 4px;
    padding: 20px;
    width: calc(20% - 20px); /* Adjust the width to fit 4-5 cards per row */
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
}

.items-list, .total-amount {
    margin-top: 10px;
}

.order-actions {
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-top: 20px;
}

.action-btn {
    background-color: #007bff;
    color: white;
    border: none;
    padding: 10px 20px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
}

.action-btn:hover {
    background-color: #0056b3;
}
