.navBar {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    height: 50px;
    max-height: 8%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* background-color: #EDEA9B; */

    padding: 7px 20px;
    z-index: 1000;
}

.yellow-def{
    background-color: #EDEA9B; 
    color: #DF8806;
}

.green{
    background-color: #A0B426;
    color: white
}

.grey{
    background-color: #535F72;
    color: white
}

@media screen and (min-width: 1024px) {
    .navBar {
        height: 50px;
    }
}

@media screen and (max-width: 768px) {
    .navBar {
        height: 40px;
    }
}

@media screen and (max-height: 480px) {
    .navBar {
        height: 40px;
        max-height: 8%;
    }
}

.navBar_Left,
.navBar_Right {
    display: flex;
    align-items: center;
}

.navBar_Left h2 {
    margin: 0;
    font-size: 22px;
    font-family: "Monoton", sans-serif;
    font-weight: 400;
    font-style: normal;
}

.signout {
    height: 30px;
    background-color: #DF8806;
    color: white;
    border: none;
    border-radius: 10px;
    align-items: center;
    cursor: pointer;
    font-size: 14px;
    padding: 5px 10px;
}

.signout p{
    margin: 0;
}

.signout:hover {
    background-color: black;
}