.containerUserCart {
    width: 90%;
    height: 535px;
    margin: 70px auto 70px auto;

    display: flex;
    flex-direction: column;
    /* align-items: center; */
}


.containerUserCart h2 {
    font-size: 20px;
    font-weight: 600;
    color: #DF8806;
    margin: 8px auto 8px 0;
}

.orderDetails {
    width: 100%;
    min-width: 100px;
    background: #EDEA9B;
    border-radius: 10px;
    padding-top: 10px;
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.1), /* Right and bottom shadow */
               0px 5px 10px rgba(0, 0, 0, 0.1); /* Bottom shadow */
}

hr {
    background-color: white;
    border: none;
    /* border-top: 5px dotted #DF8806;  */
    height: 2px;
    width: 100%;
}

.cartItem {
    width: 90%;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;

}

.cartItem h4 {
    color: #DF8806;
    margin: 8px 0;
}

.cartItemLeft {
    width: 60%;
}

.cartItemRight {
    width: 40%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.quantityCart {
    background-color: white;
    color: #DF8806;
    height: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 10px;
}

.quantityCart h4 {
    margin: 0;
}

.quantityCart button {
    background-color: transparent;
    border: none;
    padding: 0 8px;
    margin: auto 0;
    color: #DF8806;
    font-size: 18px;
    font-weight: 600;
    align-items: center;
}



.cartTotal {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 8px auto;

}

.cartTotal h4 {
    /* color: #DF8806; */
    /* margin: 0; */
    margin: 8px 15px;
}

.addMore {
    color: white;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 0 18px;
    /* padding-bottom: 8px; */
    font-size: 14px;
}

.addMore h4 {
    margin-top: 10px;
    margin-bottom: 14px;
}

.cartPlusSymbol {
    font-weight: 700;
    width: 20px;
    height: 20px;
    background-size: contain;
    background-image: url('../../../../public/images/Cart_plus_Symbol.png');
}


.slideToPay{
    margin: 0 auto;
    width: 90%;
    position: fixed;
    bottom: 70px;
}


.slide-but {
    background-color: #DF8806;
    border-radius: 20px;
}

.my-class {
    background-color: #DF8806;
    border-color: transparent;
}

